/**
 * 
 */
import React, { useCallback, useEffect, useRef } from 'react';
import moment from 'moment';
import useKeepState from 'use-keep-state';
import Table from '@/components/table';
import CreateTodo from '../components/create-todo';
import { serviceGetTodoList, serviceDeleteTodoList, serviceUpdateTodoList } from '@/services';
import { STATUS } from '../constants';
import { DatePicker, Button, Tag, Form } from 'antd';
import {
  getThisYearFirstDay,
  getCurMonthLastDay,
  modalConfirmDelete,
  ONE_DAY_TIMESTAMP
} from '@/utils';

const { RangePicker } = DatePicker;
const dateFormat = 'YYYY-MM-DD';

interface State {
  showCreateTodoModal: boolean;
  currentRowData: { [key: string]: any; } | null;
}

const initialState: State = {
  showCreateTodoModal: false,
  currentRowData: null
};

const TodoList = () => {
  const [form] = Form.useForm();
  const [state, setState] = useKeepState(initialState);
  const tableRef = useRef<any>();
  const tableColumns = [
    { title: '', dataIndex: 'status', width: 90, render: (status: number) => (
      <Tag color={STATUS[status].color}>{STATUS[status].text}</Tag>
    )},
    { title: '', dataIndex: 'createdAt', width: 170 },
    { title: '', dataIndex: 'content', className: 'word-break_break-all' },
    { title: '', width: 250, align: 'right', fixed: 'right',
      render: (row: any) => (
        <>
          <Button onClick={handleActionButton.bind(null, 0, row)}></Button>
          <Button onClick={handleActionButton.bind(null, 1, row)}></Button>
          <Button
            onClick={handleActionButton.bind(null, 2, row)}
            disabled={row.status === 2}
          >
            
          </Button>
        </>
      )
    }
  ];

  function getData() {
    tableRef.current.getTableData();
  }

  function getTodoList(params: any) {
    const values = form.getFieldsValue();

    if (values.date && values.date.length === 2) {
      params.startDate = values.date[0].valueOf();
      params.endDate = values.date[1].valueOf() + ONE_DAY_TIMESTAMP;
    }

    return serviceGetTodoList(params).then(res => {
      res.data.data.rows.map((item: any) => {
        item.createdAt = moment(item.createdAt).format('YYYY-MM-DD HH:mm');
        return item;
      });
      return res;
    });
  }

  function initParams() {
    const startDate = moment(getThisYearFirstDay(), dateFormat);
    const endDate = moment(getCurMonthLastDay(dateFormat), dateFormat);
    form.setFieldsValue({
      date: [startDate, endDate]
    });
    tableRef?.current?.getTableData();
  }

  function toggleCreateTodoModal() {
    setState({ showCreateTodoModal: !state.showCreateTodoModal });
  }

  const handleOnSuccess = function() {
    toggleCreateTodoModal();
    tableRef.current.getTableData();
  };

  const handleActionButton = useCallback((buttonType: number, row: any) => {
    switch (buttonType) {
      // 
      case 0:
        setState({ showCreateTodoModal: true, currentRowData: row });
        break;
      // 
      case 1:
        modalConfirmDelete()
        .then(() => {
          serviceDeleteTodoList(row.id)
          .then(res => {
            if (res.data.success) {
              tableRef.current.getTableData();
            }
          });
        });
        break;
      // 
      case 2:
        serviceUpdateTodoList(row.id, { status: 2 })
        .then(res => {
          if (res.data.success) {
            tableRef.current.getTableData();
          }
        });
        break;
      default:
    }
  }, [setState]);

  useEffect(() => {
    initParams();
  }, []);

  return (
    <div className="today-task">
      <div className="query-panel">
        <Form
          form={form}
          layout="inline"
          onValuesChange={() => tableRef?.current?.getTableData()}
        >
          <Form.Item name="date" label="">
            <RangePicker allowClear />
          </Form.Item>

          <Form.Item>
            <Button type="primary" onClick={getData}></Button>
            <Button onClick={initParams}></Button>
          </Form.Item>
        </Form>
      </div>

      <Table
        ref={tableRef}
        getTableData={getTodoList}
        columns={tableColumns}
        onDelete={serviceDeleteTodoList}
        onAdd={() => setState({
          showCreateTodoModal: true,
          currentRowData: null
        })}
      />
      <CreateTodo
        visible={state.showCreateTodoModal}
        onSuccess={handleOnSuccess}
        onCancel={toggleCreateTodoModal}
        rowData={state.currentRowData}
      />
    </div>
  );
};

export default TodoList;
