import React from 'react';
import useKeepState from 'use-keep-state';
import { isLtTodayTimestamp } from '@/utils';
import { serviceCreateTask } from '@/services';
import {
  Modal,
  Form,
  Input,
  DatePicker,
  Rate
} from 'antd';

type Props = {
  visible: boolean;
  data?: object;
  onSuccess: (res?: any) => void;
  onCancel: () => void;
};

const { TextArea } = Input;
const initialState = {
  confirmLoading: false,
};

const CreateTaskModal: React.FC<Props> = function ({
  visible,
  onSuccess,
  onCancel,
}) {
  const [form] = Form.useForm();
  const [state, setState] = useKeepState(initialState);

  async function handleSubmitForm() {
    try {
      const values = await form.validateFields();
      const params = {
        date: values.date.valueOf(),
        content: values.content.trim(),
        count: values.count
      };

      setState({ confirmLoading: true });

      serviceCreateTask(params)
        .then(res => {
          if (res.data.success) {
            onSuccess();
          }
        })
        .finally(() => {
          setState({ confirmLoading: false });
        });
    } catch (err) {
      console.log(err);
    }
  }

  React.useEffect(() => {
    if (!visible) {
      form.resetFields();
    }
  }, [visible]);

  return (
    <Modal
      title=""
      visible={visible}
      onOk={handleSubmitForm}
      onCancel={onCancel}
      confirmLoading={state.confirmLoading}
      forceRender
    >
      <Form form={form}>
        <Form.Item
          label=""
          name="date"
          rules={[
            {
              required: true,
              message: ""
            }
          ]}
        >
          <DatePicker
            allowClear={false}
            disabledDate={isLtTodayTimestamp}
            style={{ width: '100%' }}
          />
        </Form.Item>
        <Form.Item
          label=""
          name="content"
          rules={[
            {
              required: true,
              message: ""
            }
          ]}
        >
          <TextArea
            rows={3}
            maxLength={200}
            placeholder=""
          />
        </Form.Item>
        <Form.Item
          label=""
          name="count"
          rules={[
            {
              required: true,
              message: ""
            }
          ]}
        >
          <Rate />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default React.memo(CreateTaskModal);
