/**
 * 
 */
import React, { useEffect, useCallback, useRef } from 'react';
import Table from '@/components/table';
import moment from 'moment';
import CreateReminder from './components/create-reminder';
import useKeepState from 'use-keep-state';
import { connect } from 'react-redux';
import { DatePicker, Button, Select, Tag, Modal, Form } from 'antd';
import { serviceGetReminder, serviceDeleteReminder } from '@/services';
import {
  getThisYearLastDay,
  modalConfirmDelete,
  getThisYearFirstDay
} from '@/utils';

const { RangePicker } = DatePicker;
const Option = Select.Option;
const dateFormat = 'YYYY-MM-DD';
const STATUS_TYPE: any = {
  1: { color: '#f50', text: '' },
  2: { color: '#87d068', text: '' }
};

interface State {
  showCreateModal: boolean;
  currentRow: { [propName: string]: any } | null;
}

type Props = ReturnType<typeof mapStateToProps>;

const initialState: State = {
  showCreateModal: false,
  currentRow: null
};

const Reminder: React.FC<Props> = function({ userInfo }) {
  const [form] = Form.useForm();
  const [state, setState] = useKeepState(initialState);
  const tableRef = useRef<any>();
  const tableColumns = [
    { title: '', dataIndex: 'type', width: 100,
      render: (row: any) => (
        <Tag color={STATUS_TYPE[row].color}>{STATUS_TYPE[row].text}</Tag>
      )
    },
    { title: '', dataIndex: 'date', width: 220 },
    { title: '', dataIndex: 'content', className: 'word-break_break-all white-space_pre' },
    { title: '', width: 180, align: 'right', fixed: 'right',
      render: (row: any) => (
        <>
          <Button onClick={handleButton.bind(null, 0, row)}></Button>
          <Button onClick={handleButton.bind(null, 1, row)}></Button>
        </>
      )
    }
  ];

  const initParams = function() {
    const startDate = moment(getThisYearFirstDay(), dateFormat);
    const endDate = moment(getThisYearLastDay(), dateFormat);
    form.setFieldsValue({
      queryType: '',
      date: [startDate, endDate]
    });
    tableRef?.current?.getTableData();
  };

  function getReminder(params: any = {}) {
    const values = form.getFieldsValue();

    if (values.date && values.date.length === 2) {
      params.startDate = values.date[0].valueOf();
      params.endDate = values.date[1].valueOf();
    }

    if (values.queryType !== '') {
      params.type = values.queryType;
    }

    return serviceGetReminder(params).then(res => {
      if (res.data.success) {
        res.data.data.rows = res.data.data.rows.map((el: any, idx: number) => {
          el.order = idx + 1;
          el.date = moment(el.date).format('YYYY-MM-DD HH:mm:ss');
          return el;
        });
      }
      return res;
    });
  }

  const handleButton = useCallback((type: number, rows: any) => {
    // 
    if (type === 0) {
      setState({
        showCreateModal: true,
        currentRow: rows
      });
    } else {
      modalConfirmDelete()
      .then(() => {
        serviceDeleteReminder(rows.id)
        .then(res => {
          if (res.data.success) {
            tableRef.current.getTableData();
          }
        });
      });
    }
  }, [setState]);

  // modal
  function handleModalOnSuccess() {
    setState({ showCreateModal: false });
    tableRef.current.getTableData();
  }

  useEffect(() => {
    initParams();

    if (!userInfo.email) {
      Modal.warning({
        title: '',
        content: (
          <>
            GitHub
            <a
              href="https://github.com/settings/profile"
              target="_blank"
              rel="noopener noreferrer"
            >
              
            </a>
          </>
        ),
      });
    }
  }, [userInfo.email]);

  return (
    <div className="reminder">
      <div className="query-panel">
        <Form
          form={form}
          layout="inline"
          onValuesChange={() => tableRef?.current?.getTableData()}
        >
          <Form.Item
            name="queryType"
            label=""
            initialValue=""
          >
            <Select>
              <Option value=""></Option>
              <Option value="1"></Option>
              <Option value="2"></Option>
            </Select>
          </Form.Item>

          <Form.Item name="date" label="">
            <RangePicker allowClear />
          </Form.Item>

          <Form.Item>
            <Button type="primary" onClick={() => tableRef.current.getTableData()}></Button>
            <Button onClick={initParams}></Button>
          </Form.Item>
        </Form>
      </div>
      <Table
        ref={tableRef}
        getTableData={getReminder}
        columns={tableColumns}
        onDelete={serviceDeleteReminder}
        onAdd={() => setState({ showCreateModal: true, currentRow: null })}
      />
      <CreateReminder
        visible={state.showCreateModal}
        rowData={state.currentRow}
        onCancel={() => setState({ showCreateModal: false })}
        onSuccess={handleModalOnSuccess}
      />
    </div>
  );
};

const mapStateToProps = (store: any) => ({
  userInfo: store.user.userInfo
});

export default connect(mapStateToProps)(Reminder);
