import React, { useEffect } from 'react';
import './style.scss';
import useKeepState from 'use-keep-state';
import NoData from '@/components/no-data/index';
import TaskItem from './components/task-item';
import CreateTask from './components/create-task';
import moment from 'moment';
import { DatePicker, Button, Tag, Row, Col, Form } from 'antd';
import { getStartTimestampByDate, getEndTimestampByDate } from '@/utils';
import { serviceGetTask } from '@/services';

const dateFormat = 'YYYY-MM-DD';
const datePickerValue = moment(new Date(getStartTimestampByDate()), dateFormat);
const TASK_TYPE: any = {
  wait: { text: '', color: 'orange' },
  process: { text: '', color: '#108ee9' },
  finished: { text: '', color: '#87d068' },
  unfinished: { text: '', color: '#f50' }
};

interface State {
  data: {
    wait: any[];
    process: any[];
    finished: any[];
    unfinished: any[];
  };
  showCreateTaskModal: boolean;
}

const initialState: State = {
  data: {
    wait: [],
    process: [],
    finished: [],
    unfinished: []
  },
  showCreateTaskModal: false
};

const TodayTask = () => {
  const [form] = Form.useForm();
  const [state, setState] = useKeepState(initialState);

  function getTask() {
    const values = form.getFieldsValue();
    const date = values.startDate.valueOf();
    serviceGetTask({
      startDate: getStartTimestampByDate(date),
      endDate: getEndTimestampByDate(date)
    })
    .then(res => {
      if (res.data.success) {
        setState({ data: res.data.data });
      }
    });
  }

  function initParams() {
    form.setFieldsValue({
      startDate: datePickerValue
    });
    getTask();
  }

  function toggleCreateTaskModal() {
    setState({ showCreateTaskModal: !state.showCreateTaskModal });
  }

  function handleOnSuccess() {
    toggleCreateTaskModal();
    getTask();
  }

  useEffect(() => {
    initParams();
  }, []);

  return (
    <div className="today-task">
      <div className="query-panel">
        <Form
          form={form}
          layout="inline"
          onValuesChange={getTask}
        >
          <Form.Item name="startDate" label="">
            <DatePicker allowClear={false} />
          </Form.Item>

          <Form.Item>
            <Button type="primary" onClick={getTask}></Button>
            <Button onClick={() => setState({ showCreateTaskModal: true })}></Button>
            <Button onClick={initParams}></Button>
          </Form.Item>
        </Form>
      </div>

      <div className="wrapper">
        {(
          state.data.wait.length > 0 ||
          state.data.process.length > 0 ||
          state.data.finished.length > 0 ||
          state.data.unfinished.length > 0
        ) ? (
          <Row gutter={24}>
            {Object.keys(state.data).map((key: string) => (
              <Col span={6} key={key}>
                <div className="text-align_center">
                  <Tag color={TASK_TYPE[key].color}>{TASK_TYPE[key].text}</Tag>
                </div>
                {state.data[key].map((item: any) => (
                  <TaskItem key={item.id} data={item} reloadData={getTask} />
                ))}
              </Col>
            ))}
          </Row>
        ) : (
          <NoData
            message=""
            onClick={toggleCreateTaskModal}
          />
        )}
      </div>

      <CreateTask
        visible={state.showCreateTaskModal}
        onSuccess={handleOnSuccess}
        onCancel={toggleCreateTaskModal}
      />
    </div>
  );
};

export default TodayTask;
